html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  min-width: 320px;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  line-height: 25px;
  color: #999;
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1170px;
}

.site-container {
  overflow: hidden;
}

img {
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.is-hidden {
  display: none !important;
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  overflow: hidden;
  border: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid rgba(161, 161, 161, 0.5);
  width: 100%;
  min-height: 100px;
  color: #fff;
}

.nav {
  margin-left: 540px;
}

.nav__link {
  border-bottom: 2px solid transparent;
  padding: 40px 20px;
  transition: 0.3s;
}

.nav__link:hover {
  border-bottom: 2px solid #fff;
}

.burger {
  position: fixed;
  right: 20px;
  top: 20px;
  z-index: 10;
  display: none;
  flex-direction: column;
  align-items: center;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background-color: #f5f6f7;
  cursor: pointer;
}

.burger__line {
  position: absolute;
  display: block;
  border-radius: 4px;
  padding: 0;
  width: 18px;
  height: 2px;
  background-color: #000;
  transition: all 0.3s ease-in-out, transform 0.3s ease-in-out 0.3s;
}

.burger__line:nth-child(1) {
  top: 14px;
}

.burger__line:nth-child(2) {
  transform: rotate(90deg);
}

.burger__line:nth-child(2),
.burger__line:nth-child(3) {
  left: 19px;
  top: 19px;
  width: 1px;
}

.burger__line:nth-child(3) {
  right: 0;
}

.burger__line:nth-child(4) {
  top: 19px;
  bottom: 0;
}

.burger__line:nth-child(5) {
  bottom: 14px;
}

.burger_active .burger__line:nth-child(1) {
  top: 19px;
  background-color: transparent;
}

.burger_active .burger__line:nth-child(2) {
  left: 10.5px;
  width: 18px;
  transform: rotate(45deg);
}

.burger_active .burger__line:nth-child(3) {
  left: 10.5px;
  width: 18px;
  transform: rotate(-45deg);
}

.burger_active .burger__line:nth-child(4) {
  background-color: transparent;
}

.burger_active .burger__line:nth-child(5) {
  bottom: 19px;
  background-color: transparent;
}

.hero {
  display: flex;
  align-items: center;
  height: 100vh;
  background-image: url("./../img/hero-bg.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.hero__content {
  margin-bottom: 100px;
  max-width: 829px;
  text-align: center;
  color: #fff;
}

.hero__txt {
  line-height: 20px;
}

.hero__title {
  margin: 20px 0 34px;
  font-weight: 800;
  font-size: 48px;
  line-height: 64px;
}

.btn {
  display: inline-block;
  border-radius: 30px;
  padding: 13px 0;
  width: 200px;
  box-shadow: 0 0 10px rgba(111, 111, 111, 0.25);
  font-weight: 500;
  line-height: 20px;
  text-align: center;
  color: #fff;
  background-color: #4985ff;
  transition: 0.5s;
  cursor: pointer;
}

.btn:hover {
  background-color: #1663ff;
}

@media (max-width: 1135px) {
  .nav {
    margin-left: 0;
  }
}

@media (max-width: 768px) {
  body {
    font-size: 14px;
  }

  .header {
    display: block;
    border-bottom: none;
  }

  .nav__link {
    border-bottom: none;
    padding: 20px;
  }

  .nav__link:hover {
    border-bottom: none;
    color: #fff;
    background-color: #000;
  }

  .nav__link:first-child {
    margin-top: 80px;
  }

  .nav {
    position: absolute;
    right: 0;
    display: flex;
    flex-direction: column;
    width: 25%;
    text-align: center;
    color: #000;
    background-color: #f5f6f7;
    opacity: 0;
    visibility: hidden;
    transform: translate(0, -100%);
    transition: 0.5s;
  }

  .nav_active {
    opacity: 1;
    visibility: visible;
    transform: translate(0, 0);
  }

  .burger {
    display: flex;
  }

  .hero__title {
    margin: 20px 0 25px;
    font-size: 24px;
    line-height: 34px;
  }

  .btn {
    padding: 10px 0;
    width: 150px;
  }
}

@media (max-width: 650px) {
  .container {
    padding: 0 15px;
  }
}